@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
*{box-sizing:border-box;margin:0;padding:0}body{font-family:"Roboto",sans-serif;font-size:16px;background:#e6e9ff;margin:20px}ul{list-style:none}a{text-decoration:none}.btn{cursor:pointer;padding:5px 15px;background:#e6e9ff;color:#667aff;border:0;font-size:17px}.chat-container{max-width:1100px;background:#fff;margin:30px auto;overflow:hidden}.chat-header{background:#667aff;color:#fff;border-top-left-radius:5px;border-top-right-radius:5px;padding:15px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.chat-main{display:grid;grid-template-columns:1fr 3fr}.chat-sidebar{background:#7386ff;color:#fff;padding:20px 20px 60px;overflow-y:scroll}.chat-sidebar h2{font-size:20px;background:rgba(0,0,0,.1);padding:10px;margin-bottom:20px}.chat-sidebar h3{margin-bottom:15px}.chat-sidebar ul li{padding:10px 0}.chat-messages{padding:30px;max-height:500px;overflow-y:scroll}.chat-messages .message{padding:10px;margin-bottom:15px;background-color:#e6e9ff;border-radius:5px;overflow-wrap:break-word}.chat-messages .message .meta{font-size:15px;font-weight:bold;color:#7386ff;opacity:.7;margin-bottom:7px}.chat-messages .message .meta span{color:#777}.chat-form-container{padding:20px 30px;background-color:#667aff}.chat-form-container form{display:-webkit-flex;display:flex}.chat-form-container input[type=text]{font-size:16px;padding:5px;height:40px;-webkit-flex:1 1;flex:1 1}.join-container{max-width:500px;margin:80px auto;color:#fff}.join-header{text-align:center;padding:20px;background:#667aff;border-top-left-radius:5px;border-top-right-radius:5px}.join-main{padding:30px 40px;background:#7386ff}.join-main p{margin-bottom:20px}.join-main .form-control{margin-bottom:20px}.join-main label{display:block;margin-bottom:5px}.join-main input[type=text]{font-size:16px;padding:5px;height:40px;width:100%}.join-main select{font-size:16px;padding:5px;height:40px;width:100%}.join-main .btn{margin-top:20px;width:100%}.no-match{padding-top:150px;text-align:center}.no-match p{font-size:1.5rem}.no-match span{text-decoration:underline}@media(max-width: 700px){.chat-main{display:block}.chat-sidebar{display:none}}
